import React from "react";
import Progress from "react-progressbar";
import firebase from "../firebase";
import FileUploader from "react-firebase-file-uploader";

import { Document, Page } from "react-pdf/dist/entry.parcel";

// Setup Firebase

class ResumeUpload extends React.Component {
  state = {
    filenames: [],
    downloadURLs: [],
    isUploading: false,
    uploadProgress: 0,
    numPages: null,
    pageNumber: 1
  };

  handleUploadStart = () =>
    this.setState({
      isUploading: true,
      uploadProgress: 0
    });

  handleProgress = progress =>
    this.setState({
      uploadProgress: progress
    });

  handleUploadError = error => {
    this.setState({
      isUploading: false
      // Todo: handle error
    });
    console.error(error);
  };

  handleUploadSuccess = async filename => {
    const downloadURL = await firebase
      .storage()
      .ref("images")
      .child(filename)
      .getDownloadURL();

    this.setState(oldState => ({
      filenames: [...oldState.filenames, filename],
      downloadURLs: [...oldState.downloadURLs, downloadURL],
      uploadProgress: 100,
      isUploading: false
    }));
  };

  //pdf reader
  onDocumentLoadSuccess = ({ numPages }) => {
    this.setState({ numPages });
  };

  render() {
    return (
      <div>
        <label
          style={{
            backgroundColor: "green",
            color: "white",
            padding: 10,
            borderRadius: 4,
            pointer: "cursor"
          }}
        >
          Upload Resume
          <FileUploader
            hidden
            accept=""
            filename={file => "simple"}
            storageRef={firebase.storage().ref("images")}
            onUploadStart={this.handleUploadStart}
            onUploadError={this.handleUploadError}
            onUploadSuccess={this.handleUploadSuccess}
            onProgress={this.handleProgress}
          />
        </label>

        <Progress completed={this.state.uploadProgress} />
        <div>
          {this.state.uploadProgress === 100 ? <h3>Completed</h3> : <h3 />}
        </div>

        <p>Filename: {this.state.filenames.join(", ")}</p>

        <div>
          {this.state.downloadURLs.map((downloadURL, i) => {
            return (
              <div style={{ width: 600 }}>
                <Document
                  file={downloadURL}
                  onLoadSuccess={this.onDocumentLoadSuccess}
                >
                  <Page pageNumber={this.state.pageNumber} width={600} />
                </Document>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

export default ResumeUpload;
