import React, { Component } from "react";
import firebase from "../auth/firebase";
import { withRouter } from "react-router";
import authid from "../global";
class Home extends Component {
  constructor(props) {
    super(props);
    this.ref = firebase.firestore().collection("Jobs");
    this.unsubscribe = null;
    this.state = {
      boards: []
    };
  }

  onCollectionUpdate = querySnapshot => {
    const boards = [];
    console.log(this.props.location.state, "propsHome");
    const userId = authid.userId;

    console.log(authid.userId, "dfd");
    querySnapshot.forEach(doc => {
      const {
        title,
        jobType,
        numberJob,
        otherSpecification,
        postId
      } = doc.data();
      firebase.auth().onAuthStateChanged((user)=> {
        const uid=firebase.app().auth().currentUser.uid;
        if (user) {
           console.log("yessss")
           if (postId === firebase.app().auth().currentUser.uid) {
            boards.push({
              key: doc.id,
              doc,
              title,
              jobType,
              numberJob,
              otherSpecification,
              postId
            });
          }
           } else {
             console.log("no");
           }
         });
      

     
    });
    this.setState({
      boards
    });
  };

  componentDidMount() {
    console.log("hii");
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);
  }

  render() {
    return (
      <div>
        {/* {window.location.reload()} */}
        {this.state.boards.length !== 0 ? (
          <div className="container-fluid">
            <div className="row">
              {this.state.boards.map(board => (
                <div className="col-12 mt-3">
                  <div className="card">
                    <div className="card-horizontal">
                      <div className="img-square-wrapper">
                        {/* <img
                          className=""
                          src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTVPh-yFTXXF7kS-UHCGMkmoa7CJp9mBqLPiPSQIszewER8lQs4"
                          alt="Card image cap"
                        /> */}
                      </div>
                      <div className="card-body">
                        <h4 className="card-title">{board.title}</h4>
                        <h4 className="card-title">{board.jobType}</h4>
                        <h4 className="card-title">{board.numberJob}</h4>
                        <p className="card-text">{board.otherSpecification}</p>
                      </div>
                    </div>
                    <div className="card-footer">
                      <button className="btn btn-success">View Details</button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        ) : (
          <h1>Create Job Now</h1>
        )}
      </div>
    );
  }
}
export default Home;
