import React from "react";
import firebase from "./auth/firebase";
import { BrowserRouter as Router, Route, withRouter,Switch } from "react-router-dom";
import authid from "./global";
//Components import


import SideBar from "./CompanyComponent/sidebar";
import AddJob from "./CompanyComponent/AddJob";
import Home from "./CompanyComponent/Home";
import Profile from "./CompanyComponent/Profile";
import Logout from "./CompanyComponent/Logout";
import About from "./example";
import UserApply from "./UserComponent/Apply";
import UserProfile from "./UserComponent/Profile";
import UserHome from "./UserComponent/Home";
import UserLogout from "./UserComponent/Logout";

//-------------------------
class Autehnticated extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: ""
    };
  }
  Change = () => {
    const dat=[]
    firebase.auth().onAuthStateChanged((user)=> {
      const uid=firebase.app().auth().currentUser.uid;
      if (user) {
         console.log("yessss")
         const ref = firebase
         .firestore()
         .collection("User")
         .doc(uid);
       ref.get().then(doc => {
         if (doc.exists) {
           const userdata = doc.data();
           if (userdata.type === "User") {

             this.setState({
               value: false
             });
             this.props.history.push("/auth/users/home");
           } else if (userdata.type === "Company") {

             this.setState({
               value: true
             });
            this.props.history.push("/auth/company/home");
           }
         } else {
           console.log("no");
         }
       });
      } else {
         console.log("nooooo")
      }
    });
    console.log(authid);
    
    if(dat.uid==null || dat.uid==undefined){
      uid="nouser"
    }
    else{
      uid=dat.uid
    }
    console.log(this.props.location.state, "props");
    let uid = this.props.location.state;
    console.log(uid);
    console.log(typeof authid.userId);
   
  };
  componentDidMount() {
    this.Change();
  }

  render() {
    return (
      <div id="App">
    <SideBar pageWrapId={"page-wrap"} outerContainerId={"App"} />

    <div id="page-wrap">
      <Router>
          <Switch>
          <Route  path="/auth/company/home/" component={Home} />
          <Route exact path="/auth/company/Addjob" component={AddJob} />
          <Route exact path="/auth/company/profile" component={Profile} />
          <Route exact path="/viewUser/:id" component={About} />
          <Route exact path="/EditJob/:id" component={About} />
          <Route exact path="/auth/company/logout" component={Logout} />
         
         
            <Route exact path="/auth/users/home" component={UserHome} />
            <Route exact path="/auth/users/application" component={UserApply} />
            <Route exact path="/auth/users/profile" component={UserProfile} />
            <Route exact path="/jobs/:id" component={About} />
            <Route exact path="/auth/users/logout" component={UserLogout} />
            </Switch>
        
      
      </Router>
    </div>
  </div>
    );
  }
}

export default Autehnticated;
