import React from "react";
import firebase from "./firebase";
import swal from "sweetalert";
import "./styles.css";
import authObject from "../global";
import { Link } from "react-router-dom";

class UserForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      lemail: "",
      lpassword: "",
      semail: "",
      spassword: "",
      name: ""
    };
  }
  onChange = e => {
    const state = this.state;
    state[e.target.name] = e.target.value;
    this.setState({ board: state });
  };
  logsubmit = e => {
    e.preventDefault();

    firebase
      .auth()
      .signInWithEmailAndPassword(this.state.lemail, this.state.lpassword)
      .then(cred => {
        swal("Login sucessful!", "Welcome back", "success");
        this.props.history.push("/auth");
      })
      .catch(function(error) {
        swal("Login Failed!", "sorry", "error");
      });
  };
  Signsubmit = e => {
    e.preventDefault();
    console.log("hii");
    firebase
      .auth()
      .createUserWithEmailAndPassword(this.state.semail, this.state.spassword)
      .then(resp => {
        console.log("hiiiiii");
        swal("Signup sucessful!", "Welcome back", "success");
        firebase
          .firestore()
          .collection("User")
          .doc(resp.user.uid)
          .set({
            type: "User",
            fname: this.state.name,
            email: this.state.semail
          });
        this.props.history.push("/auth");
      })
      .catch(err => {});
  };
  render() {
    return (
      <div className="container login-container text-center">
        <h1 className="text-center">User</h1>
        <div className="row">
          <div className="col-md-6 login-form-1">
            <h3>Login Form</h3>
            <form onSubmit={this.logsubmit}>
              <div className="form-group">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Your Email *"
                  value={this.state.lemail}
                  onChange={this.onChange}
                  name="lemail"
                />
              </div>
              <div className="form-group">
                <input
                  type="password"
                  className="form-control"
                  placeholder="Your Password *"
                  value={this.state.lpassword}
                  onChange={this.onChange}
                  name="lpassword"
                />
              </div>
              <div className="form-group">
                <input type="submit" className="btnSubmit" value="Login" />
              </div>
            </form>
          </div>
          <div className="col-md-6 login-form-2">
            <h3>Sign Up</h3>
            <form onSubmit={this.Signsubmit}>
              <div className="form-group">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Your name *"
                  value={this.state.name}
                  onChange={this.onChange}
                  name="name"
                />
              </div>
              <div className="form-group">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Your Email *"
                  value={this.state.semail}
                  onChange={this.onChange}
                  name="semail"
                />
              </div>
              <div className="form-group">
                <input
                  type="password"
                  className="form-control"
                  placeholder="Your Password *"
                  value={this.state.spassword}
                  onChange={this.onChange}
                  name="spassword"
                />
              </div>
              <div className="form-group">
                <input type="submit" className="btnSubmit" value="Signup" />
              </div>
            </form>
          </div>
        </div>
        <button className="btn btn-success text" style={{ marginTop: 10 }}>
          <Link
            to="/company"
            style={{ color: "white", textDecorationLine: "none" }}
          >
            Are you company
          </Link>
        </button>
      </div>
    );
  }
}
export default UserForm;
