import React, { Component } from "react";
import firebase from "../auth/firebase";
import ResumeUpload from "./ResumeUpload";

import "./style.css";
class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userid: "",
      key: "",
      fname: "",
      lname: "",
      email: "",
      company: "",
      otherSpecification: "",
      type: ""
    };
  }
  componentDidMount() {
    const ref = firebase
      .firestore()
      .collection("User")
      .doc(firebase.auth().currentUser.uid);
    ref.get().then(doc => {
      if (doc.exists) {
        const board = doc.data();
        this.setState({
          key: doc.id,
          userId: "",
          fname: board.fname,
          lname: board.lname,
          email: board.Email,
          company: board.company,
          otherSpecification: board.other,
          type: board.type
        });
      } else {
        console.log("No such document!");
      }
    });
  }

  onChange = e => {
    const state = this.state;
    state[e.target.name] = e.target.value;
    this.setState({ board: state });
  };

  onSubmit = e => {
    e.preventDefault();

    const {
      fname,
      lname,
      email,
      company,
      otherSpecification,
      type
    } = this.state;

    const updateRef = firebase
      .firestore()
      .collection("Users")
      .doc(this.state.key);
    updateRef
      .set({
        fname,
        lname,
        email,
        company,
        otherSpecification,
        type
      })
      .then(docRef => {
        this.setState({
          fname: "",
          lname: "",
          email: "",
          company: "",
          otherSpecification: "",
          type: ""
        });
        this.props.history.push("/");
      })
      .catch(error => {
        console.error("Error adding document: ", error);
      });
  };

  render() {
    return (
      <div className="container-fluid">
        <div className="container">
          <div className="formBox">
            <form>
              <div className="row">
                <div className="col-sm-12">
                  <h1>Profile</h1>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-6">
                  <div className="inputBox ">
                    <input
                      type="text"
                      name="fname"
                      value={this.state.fname}
                      onChange={this.onChange}
                      className="input"
                      placeholder="First Name"
                    />
                  </div>
                </div>
                <div className="col-sm-6">
                  <div className="inputBox ">
                    <input
                      type="text"
                      name="lname"
                      value={this.state.lname}
                      onChange={this.onChange}
                      className="input"
                      placeholder="Last Name"
                    />
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-6">
                  <div className="inputBox">
                    <input
                      type="text"
                      name="email"
                      value={this.state.email}
                      onChange={this.onChange}
                      className="input"
                      placeholder="Email"
                    />
                  </div>
                </div>
                <div className="col-sm-6">
                  <div className="inputBox">
                    <input
                      type="text"
                      name="company"
                      value={this.state.company}
                      onChange={this.onChange}
                      className="input"
                      placeholder="Company"
                    />
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-sm-12">
                  <div className="inputBox">
                    <input
                      type="text"
                      name="other"
                      value={this.state.otherSpecification}
                      onChange={this.onChange}
                      className="input"
                      placeholder="About You"
                    />
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-sm-12">
                  <ResumeUpload/>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-12">
                  <input
                    type="submit"
                    name=""
                    className="button"
                    value="Update"
                  />
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default Profile;
