import React, { Component } from "react";
import firebase from "../auth/firebase";
class Home extends Component {
  constructor(props) {
    super(props);
    this.ref = firebase.firestore().collection("Applications");
    this.ref2 = firebase.firestore().collection("Jobs");
    this.unsubscribe = null;
    this.userId = firebase.auth().currentUser.uid;
    this.state = {
      boards: [],
      another: []
    };
  }

  onCollectionUpdate = querySnapshot => {
    const boards = [];
    querySnapshot.forEach(doc => {
      const { postId, userId } = doc.data();

      boards.push({
        postId,
        userId
      });
    });
    this.setState({
      boards
    });
    this.ref2.onSnapshot(this.onCollectionUpdateJob);
  };
  search = (nameKey, myArray) => {
    for (var i = 0; i < myArray.length; i++) {
      if (myArray[i].postId === nameKey) {
        return true;
      }
    }
  };
  onCollectionUpdateJob = querySnapshot => {
    const another = [];
    querySnapshot.forEach(doc => {
      const {
        title,
        jobType,
        numberJob,
        otherSpecification,
        postId
      } = doc.data();

      console.log(postId);
      var res = this.search(postId, this.state.boards);

      if (res) {
        console.log(title);
        another.push({
          key: doc.id,
          doc,
          title,
          jobType,
          numberJob,
          otherSpecification,
          postId
        });
        console.log(another);
      }
    });
    console.log(this.state);

    this.setState({
      another
    });
    console.log(this.state);
  };

  componentDidMount() {
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);
  }

  render() {
    return (
      <div>
        {this.state.boards.length !== 0 ? (
          <div className="container-fluid">
            <div className="row">
              {this.state.another.map(board => (
                <div className="col-12 mt-3">
                  <div className="card">
                    <div className="card-horizontal">
                      <div className="img-square-wrapper">
                        <img
                          className="img-responsive"
                          src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTVPh-yFTXXF7kS-UHCGMkmoa7CJp9mBqLPiPSQIszewER8lQs4"
                          alt="Card image cap"
                        />
                      </div>
                      <div className="card-body">
                        <h4 className="card-title">{board.title}</h4>
                        <h4 className="card-title">{board.jobType}</h4>
                        <h4 className="card-title">{board.numberJob}</h4>
                        <p className="card-text">{board.otherSpecification}</p>
                      </div>
                    </div>
                    <div className="card-footer">
                      <button className="btn btn-success">
                        Already Applied
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        ) : (
          <h1>Not Apply </h1>
        )}
      </div>
    );
  }
}
export default Home;
