import React, { Component } from "react";
import "./style.css";
import firebase from "../auth/firebase";

class AddJob extends Component {
  constructor() {
    super();
    this.ref = firebase.firestore().collection("Jobs");

    this.state = {
      postId: "",
      title: "",
      jobType: "",
      numberJob: "",
      otherSpecification: ""
    };
  }
  onChange = e => {
    const state = this.state;
    state[e.target.name] = e.target.value;
    this.setState(state);
  };

  onSubmit = e => {
    e.preventDefault();

    const { title, jobType, numberJob, otherSpecification } = this.state;
    const postId = firebase.auth().currentUser.uid;
    this.ref
      .add({
        postId,
        title,
        jobType,
        numberJob,
        otherSpecification
      })
      .then(docRef => {
        this.setState({
          postId: "",
          title: "",
          jobType: "",
          numberJob: "",
          otherSpecification: ""
        });
        this.props.history.push("/auth/company");
      })
      .catch(error => {
        console.error("Error adding document: ", error);
      });
  };
  render() {
    return (
      <div className="container-fluid">
        <div className="container">
          <div className="formBox">
            <form onSubmit={this.onSubmit}>
              <div className="row">
                <div className="col-sm-12">
                  <h1>ADD JOB</h1>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-12">
                  <div className="inputBox ">
                    <input
                      type="text"
                      name="title"
                      value={this.state.title}
                      onChange={this.onChange}
                      className="input"
                      placeholder="Job Title"
                    />
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-6">
                  <div className="inputBox">
                    <input
                      type="text"
                      name="jobType"
                      value={this.state.jobType}
                      onChange={this.onChange}
                      className="input"
                      placeholder="Job Type"
                    />
                  </div>
                </div>
                <div className="col-sm-6">
                  <div className="inputBox">
                    <input
                      type="number"
                      name="numberJob"
                      value={this.state.numberJob}
                      onChange={this.onChange}
                      className="input"
                      placeholder="Number of Postions"
                    />
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-12">
                  <div className="inputBox">
                    <textarea
                      className="input"
                      name="otherSpecification"
                      value={this.state.otherSpecifiaction}
                      onChange={this.onChange}
                      placeholder="Other Specification"
                    />
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-12">
                  <input type="submit" name="" className="button" value="ADD" />
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default AddJob;
