import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import CompanyForm from "./component/auth/CompanyForm.js";
import UserForm from "./component/auth/UserForm.js";
import Authenticated from "./component/Authenticated";
import "./styles.css";
import Homeweb from "./component/HomeCard/Home";

class App extends React.Component {
  state = {
    value: true
  };

  render() {
    return (
      <Router>
        <div>
          <Route exact path="/home" component={Homeweb} history={this.props.history} />
          <Route path="/users" component={UserForm} />
          <Route path="/company" component={CompanyForm} />
          <Route path="/auth" component={Authenticated} />
        </div>
      </Router>
    );
  }
}
const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
