import React from "react";
import { scaleRotate as Menu } from "react-burger-menu";
import { BrowserRouter as Router, Link } from "react-router-dom";

export default props => {
  return (
    // Pass on our props
    <Router>
      <Menu {...props}>
        <a className="menu-item" href="/auth/company">
          Home
        </a>

        <a className="menu-item" href="/auth/company/addjob">
          Add Job
        </a>
        <a className="menu-item" href="/auth/company/profile">
          Profile
        </a>

        <a className="menu-item" href="/auth/company/logout">
          Logout
        </a>
      </Menu>
    </Router>
  );
};
