import React, { Component } from "react";
import firebase from "../auth/firebase";
import "./style.css";
class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      key: "",
      fname: "",
      lname: "",
      email: "",
      company: "",
      about: "",
      type: ""
    };
  }
  componentDidMount() {
    firebase.auth().onAuthStateChanged((user)=> {
      if(user){
      const uid = firebase.auth().currentUser.uid;
      const ref = firebase
      .firestore()
      .collection("User")
      .doc(uid);
    ref.get().then(doc => {
      if (doc.exists) {
        const board = doc.data();
        this.setState({
          key: doc.id,
          fname: board.fname,
          lname: board.lname,
          email: board.email,
          company: board.company,
          about: board.other,
          type: board.type
        });
      } else {
        console.log("Loading");
      }
    });
      }
     else{
        console.log("loading")
     }
    })
  }

  onChange = e => {
    const state = this.state;
    state[e.target.name] = e.target.value;
    this.setState({ board: state });
  };

  onSubmit = e => {
    e.preventDefault();

    const { fname, lname, company, email, about, type } = this.state;

    const updateRef = firebase
      .firestore()
      .collection("Users")
      .doc(this.state.key);
    updateRef
      .set({
        fname,
        lname,
        about,
        email,
        company,
        type
      })
      .then(docRef => {
        this.setState({
          key: "",
          fname: "",
          lname: "",
          email: "",
          company: "",
          about: "",
          type: ""
        });
      })
      .catch(error => {
        console.error("Error adding document: ", error);
      });
  };

  render() {
    return (
      <div className="container-fluid">
        <div className="container">
          <div className="formBox">
            <form>
              <div className="row">
                <div className="col-sm-12">
                  <h1>Profile</h1>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-6">
                  <div className="inputBox ">
                    <input
                      type="text"
                      name="fname"
                      value={this.state.fname}
                      onChange={this.onChange}
                      className="input"
                      placeholder="First Name"
                    />
                  </div>
                </div>
                <div className="col-sm-6">
                  <div className="inputBox ">
                    <input
                      type="text"
                      name="lname"
                      value={this.state.lname}
                      onChange={this.onChange}
                      className="input"
                      placeholder="Last Name"
                    />
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-6">
                  <div className="inputBox">
                    <input
                      type="text"
                      name="email"
                      value={this.state.email}
                      onChange={this.onChange}
                      className="input"
                      placeholder="Email"
                    />
                  </div>
                </div>
                <div className="col-sm-6">
                  <div className="inputBox">
                    <input
                      type="text"
                      name="company"
                      value={this.state.company}
                      onChange={this.onChange}
                      className="input"
                      placeholder="Company"
                    />
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-sm-12">
                  <div className="inputBox">
                    <input
                      type="text"
                      name="about"
                      value={this.state.about}
                      onChange={this.onChange}
                      className="input"
                      placeholder="About You"
                    />
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-12">
                  <input
                    type="submit"
                    name=""
                    className="button"
                    value="Update"
                  />
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default Profile;
