import React, { Component } from "react";
import swal from "sweetalert";
import firebase from "../auth/firebase";
import "./cardStyle.css";
class Home extends Component {
  constructor(props) {
    super(props);
    this.ref = firebase.firestore().collection("Jobs");
    this.unsubscribe = null;
    this.state = {
      boards: []
    };
  }

  onCollectionUpdate = querySnapshot => {
    const boards = [];
    querySnapshot.forEach(doc => {
      const {
        title,
        jobType,
        numberJob,
        otherSpecification,
        postId
      } = doc.data();

      boards.push({
        key: doc.id,
        doc,
        title,
        jobType,
        numberJob,
        otherSpecification,
        postId
      });
    });

    this.setState({
      boards
    });
  };
  componentDidMount() {
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);
  }

  apply = id => {
    // event.preventDefault();

    const postId = id;
    firebase.auth().onAuthStateChanged((user)=> {
      if(user){
        const userId = firebase.auth().currentUser;
        const updateRef = firebase.firestore().collection("Applications");
    updateRef
      .add({
        postId,
        userId
      })
      .then(docRef => {
        swal({
          title: "Good job!",
          text: "You Applied",
          icon: "success",
          button: "Next!"
        });
        this.props.history.push("/");
      })
      .catch(error => {
        console.error("Error adding document: ", error);
      });
      }
      else{
        console.log("adad")
      }
    })

  };

  render() {
    return (
      <div className="container-fluid">
        <h1>Home</h1>
        <div className="row">
          {this.state.boards.map(board => (
            <div className="col-12 mt-3">
              <div className="card">
                <div className="card-horizontal">
                  <div className="img-square-wrapper">
                    <img
                      className=""
                      src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTVPh-yFTXXF7kS-UHCGMkmoa7CJp9mBqLPiPSQIszewER8lQs4"
                      alt="Card image cap"
                    />
                  </div>
                  <div className="card-body">
                    <h4 className="card-title">{board.title}</h4>
                    <h4 className="card-title">{board.jobType}</h4>
                    <h4 className="card-title">{board.numberJob}</h4>
                    <p className="card-text">{board.otherSpecification}</p>
                  </div>
                </div>
                <div className="card-footer">
                  <button
                    className="btn btn-success"
                    onClick={() => this.apply(board.postId)}
                  >
                    Apply
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}
export default Home;
